import React, { Component } from 'react'

import { Link } from 'react-router-dom'
import { Grid, Button, Modal, Toast } from 'antd-mobile'
// 基地址
import { BASE_URL } from '../../utils/request'

import styles from './index.module.css'

import { isToken, removeToken } from '../../utils/method'
// api
import { getUserInfo, logout } from '../../api/User'
// 菜单数据
const menus = [
  { id: 1, name: '我的收藏', iconfont: 'icon-coll', to: '/favorate' },
  { id: 2, name: '我的出租', iconfont: 'icon-ind', to: '/rent' },
  { id: 3, name: '看房记录', iconfont: 'icon-record' },
  {
    id: 4,
    name: '成为房主',
    iconfont: 'icon-identity'
  },
  { id: 5, name: '个人资料', iconfont: 'icon-myinfo' },
  { id: 6, name: '联系我们', iconfont: 'icon-cust' }
]

// 默认头像
const DEFAULT_AVATAR = BASE_URL + '/img/profile/avatar.png'

export default class Profile extends Component {
  state = {
    isLogin: isToken(),
    userInfo: null
  }
  componentDidMount() {
    // 获取个人信息
    this.getUserInfo()
  }
  // 获取个人信息
  getUserInfo = async () => {
    const { isLogin } = this.state
    if (!isLogin) {
      return
    }
    const { status, data } = await getUserInfo()


    if (status === 200) {
      data.avatar = data.avatar ? BASE_URL + data.avatar : null
      this.setState({
        userInfo: data,
      })
    } else {

      Modal.alert('提示', '当前账号未登录，是否登录？', [
        { text: '取消' },
        {
          text: '登录', onPress: () =>
            this.props.history.replace({ pathname: '/login', backUrl: this.props.location.pathname })
        },
      ]);

    }
  }
  // 登出
  logout = () => {
    Modal.alert('提示', '确认退出吗???', [
      { text: '取消' },
      {
        text: '确定', onPress: async () => {
          const { status, description } = await logout()
          if (status === 200) {
            removeToken()
            this.setState({ isLogin: false, userInfo: null }, () => {
              this.props.history.replace('/login')
            })
            Toast.success(description, .5)
          }
        }
      },
    ]);
  }
  render() {
    const { history } = this.props
    const { isLogin, userInfo } = this.state
    return (
      <div className={styles.root}>
        {/* 个人信息 */}
        <div className={styles.title}>
          <img
            className={styles.bg}
            src={BASE_URL + '/img/profile/bg.png'}
            alt="背景图"
          />
          <div className={styles.info}>
            <div className={styles.myIcon}>
              <img className={styles.avatar} src={userInfo?.avatar || DEFAULT_AVATAR} alt="icon" />
            </div>
            <div className={styles.user}>
              <div className={styles.name}>{userInfo?.nickname}</div>
              {/* 登录后展示： */}
              {isLogin ? <>
                <div className={styles.auth}>
                  <span onClick={this.logout}>退出</span>
                </div>
                <div className={styles.edit} onClick={
                  () => history.push('/rent/add')
                }>
                  发布房源
                  <span className={styles.arrow}>
                    <i className="iconfont icon-arrow" />
                  </span>
                </div>
              </> : <div className={styles.edit}>
                  <Button
                    type="primary"
                    size="small"
                    inline
                    onClick={() => history.push('/login')}
                  >
                    去登录
                </Button>
                </div>}


              {/* 未登录展示： */}

            </div>
          </div>
        </div>

        {/* 九宫格菜单 */}
        <Grid
          data={menus}
          columnNum={3}
          hasLine={false}
          renderItem={item =>
            item.to ? (
              <Link to={item.to}>
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              </Link>
            ) : (
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              )
          }
        />

        {/* 加入我们 */}
        <div className={styles.ad}>
          <img src={BASE_URL + '/img/profile/join.png'} alt="" />
        </div>
      </div>
    )
  }
}
