import React, { Component } from 'react'

// 引入antd-mobile
import { NavBar, Icon } from 'antd-mobile';

// 引入css样式
import style from './index.module.css'

// 引入当前城市位置
import { getLocalCity } from '../../utils/method'
// 获取城市房源数据
import { getHouseList, getFilterData } from '../../api/House';
import { BASE_URL } from '../../utils/request';
import HouseItem from '../../components/HouseItem'



class Map extends Component {

  state = {
    list: [],
    isShowList: false
  }

  componentDidMount() {
    // 初始化地图
    this.initMap()
  }
  // 初始化地图
  initMap = async () => {
    this.BMap = window.BMap
    this.map = new this.BMap.Map("container");
    // 创建地址解析器实例     
    let myGeo = new this.BMap.Geocoder();
    // 获取当前城市/id
    const { label: la, value } = await getLocalCity()
    // 将地址解析结果显示在地图上，并调整地图视野    
    myGeo.getPoint(null, async (point) => {
      if (point) {
        // 定位与缩放
        this.map.centerAndZoom(point, 11);
        // 添加点
        this.map.addOverlay(new this.BMap.Marker(point));
        // 添加控件
        this.map.addControl(new this.BMap.NavigationControl());
        this.map.addControl(new this.BMap.ScaleControl());
        this.map.addControl(new this.BMap.OverviewMapControl());
        this.map.addControl(new this.BMap.MapTypeControl());
        // 渲染覆盖层
        this.renderOverlay(value)
        // 地图移动事件
        this.map.addEventListener('movestart', () => {
          const { isShowList } = this.state
          if (isShowList) {
            this.setState({
              isShowList: false
            })
          }
        })
      }
    },
      la);

  }
  // 渲染覆盖层
  renderOverlay = async (value) => {
    // 根据地图缩放层级判断覆盖物类型和缩放
    const { type, zoom } = this.toTypeAndZoom()
    // 获取城市/区镇/小区/房源列表
    const { status, data } = await getHouseList(value)
    if (status === 200) {
      data.forEach(item => {
        const { coord: { longitude, latitude }, count, label: name, value } = item
        let point = new this.BMap.Point(longitude, latitude);
        // 添加自定义文本覆盖物
        let opts = {
          position: point, // 指定文本标注所在的地理位置
          offset: new this.BMap.Size(0, 0) // 设置文本偏移量
        };
        // 创建文本标注对象
        let label = new this.BMap.Label(null, opts);
        // 自定义文本标注样式
        label.setStyle({
          background: 'transparent',
          border: 0
        });
        // 判断当前层级展示不同覆盖物与状态
        let callBack
        if (type === 'cercle') {
          label.setContent(`
          <div class="${style.bubble}">
            <p class="${style.bubbleName}">${name}</p>
            <p>${count}套</p>
          </div>
          `)
          callBack = () => {
            this.map.centerAndZoom(point, zoom)
            setTimeout(() => {
              this.map.clearOverlays()
              this.renderOverlay(value)
            }, 0)
          }
        } else {
          label.setContent(`
          <div class="${style.rect}">
            <span class="${style.housename}">${name}</span>
            <span class="${style.housenum}">${count}套</span>
            <i class="${style.arrow}"></i>
          </div>
          `)
          callBack = (e) => {
            // 获取小区房源数据
            this.getCityList(value)
            // 调整文字覆盖到地图中心
            this.mapCenter(e)
          }
        }
        label.addEventListener('click', callBack)
        this.map.addOverlay(label);
      })
    }
  }
  // 根据地图缩放层级判断覆盖物类型和缩放
  // 计算要绘制的覆盖物类型和下一个缩放级别
  // 区   -> 11 ，范围：>=10 <12
  // 镇   -> 13 ，范围：>=12 <14
  // 小区 -> 15 ，范围：>=14 <16
  toTypeAndZoom = () => {
    const zooms = this.map.getZoom()
    let type, zoom;
    if (zooms >= 10 && zooms < 12) {
      type = 'cercle'
      zoom = 13
    } else if (zooms >= 12 && zooms < 14) {
      type = 'cercle'
      zoom = 15
    } else if (zooms >= 14 && zooms < 16) {
      type = 'rectangle'
    }
    return { type, zoom }
  }
  // 获取小区房源列表数据
  getCityList = async (id) => {
    const { status, data } = await getFilterData(id)
    if (status === 200) {
      this.setState({
        list: data.list,
        isShowList: true
      })
    }
  }
  // 调整文字覆盖到地图中心
  mapCenter = (e) => {
    const { clientX: startOpsX, clientY: startOpsY } = e.changedTouches[0]
    const endOpsX = window.innerWidth / 2, endOpsY = (window.innerHeight - 330) / 2
    const x = endOpsX - startOpsX, y = endOpsY - startOpsY
    this.map.panBy(x, y)
  }
  // 渲染小区下房屋列表
  renderHouseList = () => {
    return (
      <div
        className={[
          style.houseList,
          this.state.isShowList ? style.show : ''
        ].join(' ')}
      >
        <div className={style.titleWrap}>
          <h1 className={style.listTitle}>房屋列表</h1>
          <a className={style.titleMore} href="/home/house">
            更多房源
    </a>
        </div>

        <div className={style.houseItems}>
          {/* 房屋结构 */}
          {

            this.state.list.map(item => (
              <HouseItem
                onClick={() => this.props.history.push(`/detail/${item.houseCode}`)}
                key={item.houseCode}
                src={BASE_URL + item.houseImg}
                title={item.title}
                desc={item.desc}
                tags={item.tags}
                price={item.price}
              />
            ))
          }
        </div>
      </div>
    )
  }
  render() {
    return (
      <div className={style.mapbox}>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={() => this.props.history.goBack()}
        >
          地图找房
        </NavBar>
        <div id="container"></div>
        {/* 渲染小区列表 */}
        {this.renderHouseList()}
      </div>
    )
  }
}
export default Map