import request from '../../utils/request'

export function getSelected(id) {
  return request({
    method: 'get',
    url: '/houses/condition',
    params: { id }
  })
}

// 根据条件查询房屋
export function getFilterData(cityId, filter, start = 1, end = 20) {
  return request({
    method: 'get',
    url: '/houses',
    params: {
      cityId,
      ...filter,
      start,
      end
    }
  })
}
//  房屋图像上传
export function updataImage(data) {
  return request({
    method: 'post',
    url: '/houses/image',
    data
  })
}
/**
 * 
查询房源数据
 */
export function getHouseList(id) {
  return request({
    url: '/area/map',
    method: 'get',
    params: { id }
  })
}